@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,500;1,300;1,400;1,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,600;1,100;1,200;1,300;1,400;1,600&display=swap");
* {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  transition: .1s linear; }

body {
  margin: 0;
  padding: 0; }

.container {
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0; }

.header {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative; }
  .header img {
    max-width: 40px;
    max-height: 40px;
    padding: 0;
    margin: 0; }

.night {
  width: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  position: absolute;
  right: 0; }

@media screen and (max-width: 800px) {
  .header {
    width: 80%; }

  .night {
    width: 30%; } }
/* Toggle */
.toggle {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
  padding: 10px 20px;
  border-radius: 10px;
  background-color: #999795;
  cursor: pointer; }
  .toggle:checked {
    background-color: #577dfd; }
    .toggle:checked::before {
      left: 54%; }
  .toggle::before {
    content: "";
    display: block;
    padding: 8px;
    top: 11%;
    left: 6%;
    border-radius: 50%;
    background-color: white;
    position: absolute;
    transition: .1s linear; }

/* form styles */
.moon {
  width: 25px;
  height: 25px; }

form {
  width: 100%;
  margin: 50px auto 0 auto;
  display: flex;
  justify-items: center;
  align-items: center;
  text-align: center; }
  form input {
    width: 50%;
    border-radius: 10px;
    padding: 20px;
    margin: 0 auto;
    font-size: 20px;
    font-weight: 300;
    background-color: rgba(236, 236, 236, 0.678);
    position: relative;
    border: none;
    outline: none; }
  form button {
    background: none;
    border: none;
    position: absolute;
    left: 71.5%;
    cursor: pointer; }
    form button img {
      width: 30px;
      height: 30px; }

@media screen and (max-width: 800px) {
  form {
    width: 100%;
    margin: 5% auto 0 auto; }
    form input {
      width: 80%;
      border-radius: 10px;
      padding: 20px; }
    form button {
      background: none;
      border: none;
      position: absolute;
      right: 5%;
      cursor: pointer; }
      form button img {
        width: 30px;
        height: 30px; } }
/* Main text styles */
.text-wrapper {
  width: 50%;
  margin: 0 auto; }
  .text-wrapper h1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0;
    font-size: 60px;
    font-family: "Montserrat", sans-serif;
    font-weight: bold; }

@media screen and (max-width: 800px) {
  .text-wrapper {
    width: 80%; }
    .text-wrapper h1 {
      font-size: 40px; } }
.phone {
  color: #8181c9;
  font-weight: 300;
  font-size: 20px;
  margin: 0 0 50px 0;
  font-style: italic; }

/* Play audio button */
.play {
  background-color: #d1d1ff;
  cursor: pointer;
  width: 80px;
  font-size: 40px;
  text-align: center;
  color: #8181c9;
  padding: 0 0 0 5px;
  min-height: 80px;
  border: none;
  border-radius: 50%; }

.play:active {
  transform: scale(0.96); }

.play:hover {
  background-color: #8e8ef0; }

ul {
  margin: 0 20px;
  padding: 0;
  text-align: left;
  font-weight: 300;
  font-style: italic;
  font-size: 20px;
  color: #797171; }
  ul li {
    font-size: 20px;
    list-style: none;
    width: 100%;
    font-weight: 400;
    margin-bottom: 20px;
    font-style: normal;
    margin-right: 20px;
    color: black; }
    ul li::before {
      content: "";
      width: 5px;
      height: 5px;
      margin-right: 20px;
      display: inline-block;
      border-radius: 50%;
      background-color: #9b94f3; }
    ul li:first-child {
      margin-top: 20px; }

.example {
  color: #797171;
  display: block;
  margin-top: 15px;
  font-style: italic; }

span {
  font-size: 20px;
  font-weight: 300; }

.syns {
  color: #8181c9;
  font-weight: 300; }

.part {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: black;
  font-weight: bold;
  margin: 40px 0;
  font-style: italic; }
  .part::after {
    content: "";
    position: absolute;
    min-width: 45%;
    height: 1px;
    background-color: rgba(236, 236, 236, 0.678);
    margin: 10px auto 0 15px; }

@media screen and (max-width: 800px) {
  .play {
    background-color: #d1d1ff;
    cursor: pointer;
    width: 60px;
    font-size: 30px;
    min-height: 60px; }

  ul {
    font-size: 15px;
    color: #797171; }
    ul li {
      font-size: 15px; }

  span {
    font-size: 15px; }

  .part {
    font-family: "Montserrat", sans-serif;
    font-size: 13px; }
    .part::after {
      content: "";
      position: absolute;
      min-width: 60%;
      height: 1px;
      background-color: rgba(236, 236, 236, 0.678);
      margin: 10px auto 0 15px; } }
/*  */
.modal {
  top: 0;
  position: absolute;
  width: 100%;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
  z-index: 1000;
  opacity: 1;
  background-color: rgba(39, 34, 34, 0.7);
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  transition: .2s linear; }

.hide {
  opacity: 0;
  z-index: -100; }

@media (min-width: 800px) and (max-width: 1300px) {
  .header {
    width: 60%; }

  .night {
    width: 15%; }

  form {
    width: 75%;
    margin: 5% auto 0 auto; }
    form input {
      width: 80%;
      border-radius: 10px;
      padding: 20px; }
    form button {
      background: none;
      border: none;
      position: absolute;
      right: 20%;
      cursor: pointer; }
      form button img {
        width: 30px;
        height: 30px; }

  .text-wrapper {
    width: 60%; }
    .text-wrapper h1 {
      font-size: 40px; }

  .play {
    background-color: #d1d1ff;
    cursor: pointer;
    width: 60px;
    font-size: 30px;
    min-height: 60px; }

  ul {
    font-size: 15px;
    color: #797171; }
    ul li {
      font-size: 15px; }

  span {
    font-size: 15px; }

  .part {
    font-family: "Montserrat", sans-serif;
    font-size: 13px; }
    .part::after {
      content: "";
      position: absolute;
      min-width: 50%;
      height: 1px;
      background-color: rgba(236, 236, 236, 0.678);
      margin: 10px auto 0 15px; } }

